.form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    align-items: center;
  }
  .formLabel {
    display: flex;
    flex-direction: column;
    gap: 5px;
    font-size: 18px;
    font-weight: 700;
  }
  
  .formInput {
    width: 250px;
    height: 20px;
    border-radius: 4px;
    border: none;
    font-size: 18px;
    font-weight: 600;
    color: rgb(114, 114, 114);
    padding: 5px 10px;
    outline: transparent;
  }
  
  .registerBtn {
      border-radius: 4px;
      padding: 10px 30px;
      border: none;
      cursor: pointer;
      font-weight: 600;
    }
    
    .registerBtn:hover,
    .registerBtn:focus {
      background-color: rgb(17, 189, 55);
      color: #fff;
      transform: scale(1.1);
      transition: all 250ms;
    }

